@import "../resources/_data"

.Loading__Container
  width: 100%
  height: 100vh
  background: $dark-gray
  display: flex
  justify-content: center
  align-items: center

.Loading-Initial
  display: flex
  flex-direction: column
  align-items: center
  animation: appear 1s
  &__Image-Container
    width: 255px
    height: 195px
    display: flex
    flex-direction: column
    justify-content: space-between
    align-items: center
  &__Image-Icon
    width: 60%
    animation: loading-icon 1s 4.5s forwards, disappear 1.5s 5.5s forwards
  &__Image-Text
    width: 100%
    animation: disappear 1s 3.5s forwards

  &__Bar
    height: 30px
    margin: 110px auto 0
    display: grid
    grid-template-columns: repeat(7, 15px)
    column-gap: 10px
    animation: disappear 1s 3.5s forwards
    :nth-child(1)
      animation-delay: 1s
    :nth-child(2)
      animation-delay: 1.25s
    :nth-child(3)
      animation-delay: 1.5s
    :nth-child(4)
      animation-delay: 1.75s
    :nth-child(5)
      animation-delay: 2s
    :nth-child(6)
      animation-delay: 2.25s
    :nth-child(7)
      animation-delay: 2.5s
    &-Item
      width: 15px
      height: 30px
      border-radius: 5px
      background: $dark-gray
      animation: loading-bar 1s 1s forwards

.Loading-Finish
  width: 800px
  height: 420px
  display: flex
  justify-content: center
  align-items: center
  position: relative
  animation: appear 2s
  &--Animated
    @extend .Loading-Finish
    .Loading-Finish__Logo-Container
      animation: disappear 1s forwards
    .Loading-Finish__Visor-Container
      animation: zoom-visor 1s 1s forwards
    .Loading-Finish__Visor-Image
      animation: zoom-image 1s 1s forwards
    .Loading-Finish__Button
      animation: loading-button 1s forwards, zoom-button 1s 1s forwards
      div
        display: none
    .Loading-Finish__Text
      animation: disappear 1s forwards
  &__Logo-Container
    position: absolute
    bottom: calc(100% - 20px)
    left: -160px
  &__Logo
    width: 390px
    height: 155px
  &__Visor-Container
    width: 100%
    height: 100%
    border-radius: 30px
    position: relative
    overflow: hidden
  &__Visor-Image
    width: 100vw
    height: 100vh
    aspect-ratio: 16/9
    position: absolute
    top: calc(-50vh + 210px)
    left: calc(-50vw + 400px)
  &__Button
    width: 200px
    height: 200px
    outline: 20px solid $dark-gray
    border-radius: 20px
    background: $cyan-1
    display: flex
    justify-content: center
    align-items: center
    position: absolute
    top: calc(100% - 40px)
    left: calc(50% - 100px)
    transform: rotate(45deg)
    &:hover
      background: $cyan-2
  &__Button-Text
    width: 190px
    display: block
    color: white
    font-family: Font-Bold
    font-size: 30px
    transform: rotate(-45deg)
  &__Text
    color: $gray-2
    font-family: Font-Bold
    font-size: 18px
    position: absolute
    top: calc(100% + 10px)
    right: 0

.Menu__Container
  width: 100vw
  height: 100vh
  background: $black-shadow
  display: flex
  justify-content: center
  align-items: center
  animation: appear 1s

.Menu
  display: grid
  grid-template-rows: repeat(2, 110px)
  row-gap: 15px
  &__Button
    width: 375px
    height: 110px
    display: flex
    justify-content: center
    align-items: center
    color: white
    font-family: Font-Bold
    font-size: 32px
    &--Cyan
      @extend .Menu__Button
      border-radius: 25px 25px 0 0
      background: $cyan-2
      &:hover
        background: $cyan-3
    &--Gray
      @extend .Menu__Button
      border-radius: 0 0 25px 25px
      background: $gray-1
      &:hover
        background: $dark-gray

@keyframes appear
  0%
    opacity: 0
  100%
    opacity: 1

@keyframes disappear
  0%
    opacity: 1
  100%
    opacity: 0

@keyframes loading-bar
  0%
    background: $dark-gray
  33%
    background: $cyan-3
  66%
    background: $cyan-2
  100%
    background: $cyan-1

@keyframes loading-icon
  0%
    width: 153px
    margin-top: 0
  100%
    width: 300px
    margin-top: 40px

@keyframes loading-button
  0%
    background-color: $cyan-2
  100%
    background-color: $dark-gray

@keyframes zoom-visor
  0%
    width: 100%
    height: 100%
    border-radius: 30px
    position: absolute
  100%
    width: 100vw
    height: 100vh
    border-radius: 0
    position: absolute

@keyframes zoom-image
  0%
    top: calc(-50vh + 210px)
    left: calc(-50vw + 400px)
  100%
    top: 0
    left: 0

@keyframes zoom-button
  0%
    opacity: 1
    top: calc(100% - 40px)
  100%
    display: none
    opacity: 0
    top: 200%