@import "../resources/_data"

.Step_5
  width: 105px
  height: 285px
  border-radius: 15px
  padding: 15px
  box-shadow: 0 0 25px rgba(0, 0, 0, .2)
  background: white
  display: flex
  flex-direction: column
  justify-content: space-between
  align-items: center
  position: absolute
  top: 50px
  left: 50px
  animation: appear 1s
  &__Button
    width: 70px
    height: 70px
    border-radius: 10px
    padding: 10px
    background: $gray-2
    display: flex
    justify-content: center
    align-items: center
    &--Cyan
      @extend .Step_5__Button
      background: $cyan-1
  &__Icon
    width: 100%
    height: 100%
  &__Arrow
    height: 28px
  .Caption
    width: 900px
    height: 44px
    display: flex
    justify-content: flex-start
    align-items: center
    position: absolute
    top: calc(47px - 22px)
    left: calc(100% + 20px)
    color: white