@import "../resources/_data"

.Step_1
  display: flex
  flex-direction: column
  justify-content: center
  align-items: center
  color: white
  &__Icon--Camera
    width: 45px
    height: 60px
  &__Image-Container
    width: 700px
    height: 450px
    border-radius: 100px
    background: white
    position: relative
    overflow: hidden
  &__Image
    width: 150%
    height: 150%
    top: -25%
    left: -25%
    position: absolute
    animation: camera 10s infinite
  .Mouse
    animation: mouse 10s infinite

@keyframes camera
  0%
    transform: translateX(0px)
  12%
    transform: translateX(0px)
  24%
    transform: translateX(100px)
  36%
    transform: translateX(-100px)
  48%
    transform: translateX(0)
  60%
    transform: translateY(50px)
  72%
    transform: translateY(-50px)
  84%
    transform: translateY(0)
  100%
    transform: translateY(0)

@keyframes mouse
  0%
    transform: scale(1.2)
  12%
    transform: scale(.8)
  24%
    transform: scale(.8) translateX(-250px)
  36%
    transform: scale(.8) translateX(250px)
  48%
    transform: scale(.8) translateX(0)
  60%
    transform: scale(.8) translateY(-150px)
  72%
    transform: scale(.8) translateY(150px)
  84%
    transform: scale(.8) translateY(0)
  100%
    transform: scale(1.2)