*
  box-sizing: border-box

body
  width: 100%
  height: 100vh
  margin: 0
  border: 0
  padding: 0

main
  width: 100%
  height: 100vh
  background-image: url("./assets/images/background.jpg")
  background-size: 100vw 100vh
  display: flex
  justify-content: center
  align-items: center

button
  border: unset
  padding: unset
  cursor: pointer
  transition: background-color .2s

a
  border: unset
  padding: unset
  text-decoration: unset
  cursor: pointer
  transition: background-color .2s

.hide
  display: none