@import "../resources/_data"

.Step_3
  display: flex
  justify-content: center
  align-items: center
  color: white
  animation: appear 1s
  &__Content
    display: flex
    flex-direction: column
    justify-content: center
    align-items: center
  &__Icon--Keyboard
      width: 100px
  &__Image-Container
    width: 700px
    height: 450px
    border-radius: 100px
    background: white
    position: relative
    overflow: hidden
  &__Image
    width: 150%
    height: 150%
    top: -25%
    left: -25%
    position: absolute
    animation: navigation 10s 1s infinite
  &__Key-Container
    margin: 100px 0 0 200px
    display: grid
    grid-template-columns: repeat(3, 85px)
    grid-template-rows: repeat(2, 85px)
    grid-template-areas:". up ." "left bottom right" "text text text"
    gap: 15px
    :nth-child(1)
      grid-area: up
    :nth-child(2)
      grid-area: left
      animation-delay: 6s
    :nth-child(3)
      grid-area: bottom
      animation-delay: 3.5s
    :nth-child(4)
      grid-area: right
      animation-delay: 8.5s
    :nth-child(5)
      grid-area: text
      justify-self: center
  &__Key
    width: 85px
    height: 85px
    border-radius: 15px
    background: $cyan-3
    display: flex
    justify-content: center
    align-items: center
    color: white
    font-size: 36px
    font-family: Font-Bold
    animation: key 10s 1s infinite
  &__Key-Text
    color: $cyan-1
    font-size: 32px
    font-family: Font-Bold

@keyframes navigation
  0%
    transform: scale(1)
  12%
    transform: scale(1.25)
  25%
    transform: scale(1)
  37%
    transform: scale(.75)
  50%
    transform: scale(1)
  62%
    transform: translateX(120px)
  75%
    transform: translateX(0)
  87%
    transform: translateX(-120px)
  100%
    transform: translateX(0)

@keyframes key
  0%
    background: $cyan-3
  5%
    background: $cyan-1
  15%
    background: $cyan-1
  20%
    background: $cyan-3