@import "../resources/_data"

.Step_9
  width: 105px
  height: 285px
  border-radius: 15px
  padding: 15px
  box-shadow: 0 0 25px rgba(0, 0, 0, .2)
  background: white
  display: flex
  flex-direction: column
  justify-content: space-between
  align-items: center
  position: absolute
  top: 50px
  left: 50px
  &__Button
    width: 70px
    height: 70px
    border-radius: 10px
    padding: 10px
    background: $gray-3
    display: flex
    justify-content: center
    align-items: center
    animation: config-button-unpress 1s 1s forwards
    &:hover
      background: $cyan-1
  &__Icon
    width: 100%
    height: 100%
  &__Arrow-Container
    width: 900px
    height: 44px
    display: flex
    justify-content: flex-start
    align-items: center
    position: absolute
    color: white
    animation: appear 1s
  &__Arrow
    height: 28px

  .Config-Items__Container
    width: 0px
    height: 86px
    border-radius: 0 43px 43px 0
    background: white
    display: grid
    grid-template-columns: repeat(3, 70px)
    column-gap: 20px
    justify-content: center
    align-content: center
    position: absolute
    left: 98%
    z-index: -1
    &--One-Disappear
      @extend .Config-Items__Container
      width: 275px
      opacity: 1
      top: 7px
      animation: config-item-disappear 1s 2s forwards
      .Config__Item
        animation: disappear 1s 1s forwards
    &--Two-Disappear
      @extend .Config-Items__Container
      width: 275px
      opacity: 1
      top: calc(50% - 43px)
      animation: config-item-disappear 1s 2s forwards
      .Config__Item
        animation: disappear 1s 1s forwards
    &--Three-Disappear
      @extend .Config-Items__Container
      width: 275px
      opacity: 1
      bottom: 7px
      animation: config-item-disappear 1s 2s forwards
      .Config__Item
        animation: disappear 1s 1s forwards

  .Config__Item
    width: 70px
    height: 70px
    opacity: 1

  ~ .Sofa-Container
    display: flex
    flex-direction: column
    justify-content: space-between
    align-items: center
    position: absolute
    top: calc( 50% - 130px )
    left: calc( 50% - 272px )
    .Sofa-1
      opacity: 1
      animation: sofa-border .5s 3s forwards
    .Caption
      display: flex
      flex-direction: column
      justify-items: center
      align-items: center
      color: white
      opacity: 0
      animation: appear 1s 2s forwards
    .Config__Arrow
      margin-bottom: 10px
      transform: rotate(90deg)
    .Mouse
      top: 50%
      left: calc(100% + 100px)
      opacity: 0
      animation: sofa-mouse 2s 2s forwards

.Change-Sofa__Container
  width: 190px
  height: 120px
  border-radius: 0 20px 20px 0
  padding: 15px
  background: white
  display: flex
  flex-direction: column
  justify-content: space-between
  align-items: flex-end
  position: absolute
  bottom: 50px
  left: -100%
  animation: change-sofa-container 1s 3s forwards
  .Caption
    width: 900px
    height: unset
    display: flex
    justify-content: flex-start
    align-items: center
    position: absolute
    position: absolute
    left: calc(100% + 20px)
    opacity: 0
    animation: appear 1s 4s forwards
  .Mouse
    opacity: 0
    top: -20px
    left: calc(100% + 20px)
    z-index: 10
    animation: change-sofa-mouse 5s 4s forwards
  .Change-Sofa__Button
    width: 120px
    height: 70px
    border-radius: 15px
    padding: 10px
    background: $gray-2
    display: flex
    justify-content: center
    align-items: center
    animation: change-sofa-button 1s 4s forwards
  .Change-Sofa__Icon
    height: 50px
  .Change-Sofa__Text
    width: 120px
    text-align: center
    color: $cyan-2
    font-family: Font-Bold
    font-size: 16px
  .Change-Sofa__Add-Button
    width: 35px
    height: 35px
    border-radius: 40px
    border: 6px solid $cyan-1
    display: flex
    justify-content: center
    align-items: center
    position: absolute
    top: -18px
    right: -18px
  .Change-Sofa__Cruz
    width: 30px
    height: 30px
    opacity: 0

@keyframes config-button-unpress
  0%
    background: $gray-3
  100%
    background: $gray-2

@keyframes config-item
  0%
    width: 0
  100%
    width: 275px

@keyframes config-item-disappear
  0%
    width: 275px
  100%
    width: 0

@keyframes sofa-border
  0%
    filter: drop-shadow(0 0 0 transparent)
  100%
    filter: drop-shadow(0 0 4px white) drop-shadow(0 0 4px white) drop-shadow(0 0 4px white)

@keyframes sofa-mouse
  0%
    left: calc(100% + 100px)
    opacity: 0
  20%
    left: calc(100% + 100px)
    opacity: 1
  40%
    left: 50%
    transform: scale(1)
  60%
    transform: scale(.8)
  80%
    left: 50%
    transform: scale(1)
    opacity: 1
  100%
    left: calc(100% + 100px)
    opacity: 0

@keyframes change-sofa-container
  0%
    left: -100%
  100%
    left: 0

@keyframes change-sofa-button
  0%
    background: $gray-2
  100%
    background: $cyan-2

@keyframes change-sofa-mouse
  0%
    opacity: 0
  20%
    opacity: 1
    top: -20px
    left: calc(100% + 20px)
  40%
    top: 50px
    left: calc(100% - 75px)
    transform: scale(1)
  60%
    transform: scale(.8)
  80%
    opacity: 1
    top: 50px
    left: calc(100% - 75px)
    transform: scale(1)
  90%
    opacity: 1
    top: -20px
    left: calc(100% + 20px)
  100%
    opacity: 0