@import "../resources/_data"

.Step_4
  display: flex
  flex-direction: column
  justify-content: center
  align-items: center
  color: white
  &__Icon--Floor
      width: 50px
  &__Image-Container
    width: 700px
    height: 450px
    border-radius: 100px
    background: white
    position: relative
    overflow: hidden
  &__Image
    width: 150%
    height: 150%
    top: -25%
    left: -25%
    position: absolute
    animation: floor 10s infinite
  &__Circle
    width: 80px
    position: absolute
    top: calc( 90% - 45px )
    left: calc(25% - 50px)
    opacity: 0
    animation: floor-circle 10s infinite
  .Mouse
    width: 30px
    height: 46px
    top: calc( 90% - 23px )
    left: calc( 50% - 15px )
    position: absolute
    animation: floor-mouse 10s infinite

@keyframes floor
  0%
    transform: scale(1) translateX(0)
  50%
    transform: scale(1) translateX(0)
  51%
    transform: scale(2) translateX(100px)
  70%
    transform: scale(2) translateX(100px)
  80%
    transform: scale(1) translateX(0)

@keyframes floor-mouse
  0%
    left: calc( 50% - 15px )
  20%
    left: calc( 25% - 15px )
    transform: scale(1)
  40%
    left: calc( 25% - 15px )
    transform: scale(.8)
  41%
    transform: scale(1)
  60%
    left: calc( 25% - 15px )
    transform: scale(1)
  70%
    left: calc( 25% - 15px )
    transform: scale(1)
  80%
    left: calc( 50% - 15px )

@keyframes floor-circle
  40%
    opacity: 0
    transform: scale(.5)
  41%
    opacity: 1
    transform: scale(1)
  50%
    opacity: 1
    transform: scale(1)
  51%
    opacity: 0
    transform: scale(.5)