@import '../resources/_data'

.Step_10
  width: 105px
  height: 285px
  border-radius: 15px
  padding: 15px
  box-shadow: 0 0 25px rgba(0, 0, 0, .2)
  background: white
  display: flex
  flex-direction: column
  justify-content: space-between
  align-items: center
  position: absolute
  top: 50px
  left: 50px
  &__Button
    width: 70px
    height: 70px
    border-radius: 10px
    padding: 10px
    background: $gray-2
    display: flex
    justify-content: center
    align-items: center
    &:hover
      background: $cyan-1
  &__Icon
    width: 100%
    height: 100%
  &__Arrow-Container
    width: 900px
    height: 44px
    display: flex
    justify-content: flex-start
    align-items: center
    position: absolute
    color: white
    animation: appear 1s
  &__Arrow
    height: 28px

  .Config-Items__Container
    width: 0px
    height: 86px
    border-radius: 0 43px 43px 0
    background: white
    display: none
    grid-template-columns: repeat(3, 70px)
    column-gap: 20px
    justify-content: center
    align-content: center
    position: absolute
    left: 98%
    z-index: -1

  .Config__Item
    display: none
    width: 70px
    height: 70px
    opacity: 0

  ~ .Sofa-Container
    display: flex
    flex-direction: row
    justify-content: space-between
    align-items: center
    position: static
    .Sofa-1
      opacity: 1
      animation: sofa-border-disappear 1s forwards
    .Sofa-2
      margin-left: 30px
      opacity: 0
      animation: sofa-appear 1s 2s forwards
    .Caption
      display: flex
      flex-direction: column
      justify-items: center
      align-items: center
      color: white
      opacity: 0
      animation: appear 1s 2s forwards
    .Config__Arrow
      margin-bottom: 10px
      transform: rotate(90deg)
    .Mouse
      top: 50%
      left: calc(100% + 100px)
      opacity: 0
      animation: unset

.Step_10__Change-Sofa__Container
  width: 190px
  height: 120px
  border-radius: 0 20px 20px 0
  padding: 15px
  background: white
  display: flex
  flex-direction: column
  justify-content: space-between
  align-items: flex-end
  position: absolute
  bottom: 50px
  left: 0px
  amimation: unset
  .Caption
    width: 900px
    height: unset
    display: flex
    justify-content: flex-start
    align-items: center
    position: absolute
    position: absolute
    left: calc(100% + 20px)
    opacity: 0
    animation: appear 1s forwards
  .Mouse
    opacity: 1
    top: -10px
    left: unset
    right: -100px
    z-index: 100
    animation: mouse-add-sofa 2s 1s forwards
  .Change-Sofa__Button
    width: 120px
    height: 70px
    border-radius: 15px
    padding: 10px
    background: $cyan-2
    display: flex
    justify-content: center
    align-items: center
  .Change-Sofa__Icon
    height: 50px
  .Change-Sofa__Text
    width: 120px
    text-align: center
    color: $cyan-2
    font-family: Font-Bold
    font-size: 16px
  .Change-Sofa__Add-Button
    width: 35px
    height: 35px
    border-radius: 40px
    border: 6px solid $cyan-1
    display: flex
    justify-content: center
    align-items: center
    position: absolute
    top: -18px
    right: -18px
    animation: change-sofa-add-button 1s 1s forwards
  .Change-Sofa__Cruz
    width: 30px
    height: 30px
    opacity: 0
    animation: change-sofa-cruz 1s 1s forwards


@keyframes sofa-border-disappear
  0%
    filter: drop-shadow(0 0 4px white) drop-shadow(0 0 4px white) drop-shadow(0 0 4px white)
  100%
    filter: drop-shadow(0 0 0 transparent)

@keyframes mouse-add-sofa
  0%
    top: -5px
    right: -100px
  33%
    top: -5px
    right: -25px
    transform: scale(1)
  66%
    transform: scale(.8)
  100%
    top: -5px
    right: -25px
    transform: scale(1)

@keyframes change-sofa-add-button
  0%
    width: 35px
    height: 35px
    border: 6px solid $cyan-1
    top: -18px
    right: -18px
  100%
    width: 50px
    height: 50px
    border: 4px solid $cyan-1
    top: -25px
    right: -25px

@keyframes change-sofa-cruz
  0%
    opacity: 0
  100%
    opacity: 1