@import "../resources/_data"

.Step_8
  width: 105px
  height: 285px
  border-radius: 15px
  padding: 15px
  box-shadow: 0 0 25px rgba(0, 0, 0, .2)
  background: white
  display: flex
  flex-direction: column
  justify-content: space-between
  align-items: center
  position: absolute
  top: 50px
  left: 50px
  :nth-child(1)
    animation-delay: 1.5s, 10s
  :nth-child(2)
    animation-delay: 3s, 10s
  :nth-child(3)
    animation-delay: 4.5s, 10s
  &__Button
    width: 70px
    height: 70px
    border-radius: 10px
    padding: 10px
    background: $gray-2
    display: flex
    justify-content: center
    align-items: center
    animation: config-button-press 1s forwards
    &:hover
      background: $cyan-1
    &--Cyan
      @extend .Step_8__Button
      background: $cyan-1
  &__Icon
    width: 100%
    height: 100%
  &__Arrow-Container
    width: 900px
    height: 44px
    display: flex
    justify-content: flex-start
    align-items: center
    position: absolute
    color: white
    animation: appear 1s
  &__Arrow
    height: 28px

  .Mouse
    top: calc( 50% - 23px )
    left: calc( 100% + 25px )
    animation: appear 1s, config-mouse-1 5s 1s forwards, config-mouse-2 3s 7s forwards

  .Caption
    width: 900px
    height: 44px
    display: flex
    justify-content: flex-start
    align-items: center
    position: absolute
    color: white
    top: calc(47px - 22px)
    left: calc(100% + 20px)
    animation: disappear 1s 1s forwards

  .Caption-2
    @extend .Caption
    left: calc(100% + 285px)
    opacity: 0
    animation: appear 1s 6s forwards

  .Config-Items__Container
    width: 0px
    height: 86px
    border-radius: 0 43px 43px 0
    background: white
    display: grid
    grid-template-columns: repeat(3, 70px)
    column-gap: 20px
    justify-content: center
    align-content: center
    position: absolute
    left: 98%
    z-index: -1
    &--One
      @extend .Config-Items__Container
      top: 7px
      animation: config-item 1s 1.5s forwards
      .Config__Item
        animation-delay: 2.5s, 9.5s
    &--Two
      @extend .Config-Items__Container
      top: calc(50% - 43px)
      animation: config-item 1s 3s forwards
      .Config__Item
        animation-delay: 4s, 9.5s
    &--Three
      @extend .Config-Items__Container
      bottom: 7px
      animation: config-item 1s 4.5s forwards
      .Config__Item
        animation-delay: 5.5s

  .Config__Item
    width: 70px
    height: 70px
    opacity: 0
    animation: appear .5s forwards

.Sofa-Container
  display: flex
  flex-direction: column
  justify-content: space-between
  align-items: center
  position: absolute
  top: calc( 50% - 130px )
  left: calc( 50% - 272px )

.Sofa-1
  width: 545px
  aspect-ratio: 16/9
  opacity: 0
  animation: sofa-appear 1s 6s forwards

@keyframes config-mouse-1
  0%
    top: calc( 50% - 23px )
    left: calc( 100% + 25px )
    transform: scale(1)
  10%
    top: 47px
    left: 50%
    transform: scale(1)
  20%
    transform: scale(.8)
  30%
    top: 47px
    transform: scale(1)
  40%
    top: 142px
    transform: scale(1)
  50%
    transform: scale(.8)
  60%
    top: 142px
    transform: scale(1)
  70%
    top: 238px
    transform: scale(1)
  80%
    transform: scale(.8)
  90%
    top: 238px
    transform: scale(1)
    opacity: 1
  100%
    top: 100%
    left: 50%
    opacity: 0

@keyframes config-mouse-2
  0%
    top: 100px
    left: calc( 100% + 325px )
    opacity: 0
  10%
    top: 100px
    opacity: 1
  20%
    top: 100px
    left: calc( 100% + 325px )
  40%
    top: 50px
    left: calc( 100% + 135px )
    transform: scale(1)
  60%
    transform: scale(.8)
  80%
    top: 50px
    transform: scale(1)
    left: calc( 100% + 135px )
    opacity: 1
  100%
    top: 100px
    left: calc( 100% + 135px )
    opacity: 0

@keyframes config-button-press
  0%
    background: $gray-2
  50%
    background: $gray-3
  100%
    background: $gray-3

@keyframes config-item
  0%
    width: 0
  100%
    width: 275px

@keyframes sofa-appear
  0%
    opacity: 0
    transform: scale(0)
  100%
    opacity: 1
    transform: scale(1)